<template>
  <div class="download">
    <h1>Download Mew Pew</h1>
    <h3>The space simulator</h3>

    <br />
    <br />

    <select id="selectOS" v-model="os">
      <option>Windows(x86x64) <i class="fab fa-windows" /></option>
      <option>Linux <i class="fab fa-linux" /></option>
    </select>

    <br /><br />

    <template v-if="os== 'Windows(x86x64)'">
      <button type="button" class="btn site-btn" @click="downloadMewPew('win')">Click Here <i class="fab fa-windows" /></button>
    </template>
    <template v-if="os== 'Linux'">
      <button type="button" class="btn site-btn" @click="downloadMewPew('linux')">Click Here <i class="fab fa-linux" /></button>
    </template>

  </div>
</template>

<script>

  import viewsFuctions from '../controllers/views'
  export default {
  
    name: 'download',
    data() {
      return {
        os: ""
      }
    },
    mounted() {
      viewsFuctions.updateButtons(this.pagesList, this.$options.name)
      const osLocal = navigator.appVersion
      if (osLocal.indexOf("Win") != -1)
        this.os = "Windows(x86x64)"
      else if (osLocal.indexOf("Linux") != -1)
        this.os = "Linux"
      else
        this.os = "none"
    },
    methods: {
      downloadMewPew(os) {
        console.log(os)
        let link = this.$route.params.proxy + "/downloadmewpew"
        if (os == "linux")
          link += "_linux"
        window.open(link)
      }
    },
    props: ["pagesList"]
}
</script>

<style scoped>
  h1 {
      text-align: center;
      text-decoration: underline
  }

  h3 {
      text-align: center;
  }

  .site-btn {
      margin-left: 7vw;
  }

  select {
      margin-left: 6vw;
  }
</style>
